import React, { Component } from 'react';
import { Card, Table, Modal, Button, message } from 'antd';
import axios from './../../axios';
import Utils from './../../utils';

class BasicTable extends Component {
    state = {
        dataSource2: []
    };

    params = {
        page: 1
    };

    componentDidMount() {
        let data = [
            {
                id: '0',
                userName: 'Jack',
                sex: '1',
                state: '1',
                interest: '1',
                birthday: '2000-01-01',
                address: '',
                time: '09:00'
            },
            {
                id: '1',
                userName: 'Tom',
                sex: '1',
                state: '1',
                interest: '1',
                birthday: '2000-01-01',
                address: '',
                time: '09:00'
            },
            {
                id: '2',
                userName: 'Lily',
                sex: '1',
                state: '1',
                interest: '1',
                birthday: '2000-01-01',
                address: '',
                time: '09:00'
            }
        ];
        data = data.map((item, index) => ({
            ...item,
            key: index
        }));
        this.setState({
            dataSource: data
        });
        this.request();
    }

    // mock
    request = () => {
        let _this = this;
        axios.ajax({
            url: '/table/list',
            data: {
                params: {
                    page: this.params.page
                }
            }
        }).then(res => {
            if (res.code === 0) {
                res.result.list = res.result.list.map((item, index) => ({
                    ...item,
                    key: index
                }));
                this.setState({
                    dataSource2: res.result.list,
                    selectedRowKeys: [],
                    selectedRows: null,
                    selectedCheckRowKeys: [],
                    selectedCheckRows: null,
                    pagination: Utils.pagination(res, current => {
                        _this.params.page = current;
                        this.request();
                    })
                });
            }
        });
    };

    onRowClick = (record, index) => {
        const selectedRowKeys = [index];

        Modal.info({
            title: '',
            content: `${record.userName},${record.interest}`
        });
        this.setState({
            selectedRowKeys,
            selectedRows: record
        });
    };

    // 
    handleDelete = () => {
        const rows = this.state.selectedCheckRows;
        const ids = rows.map(item => item.id);
        Modal.info({
            title: '',
            content: `${ids.join(',')}`,
            onOk: () => {
                message.success('');
                this.request();
            }
        })
    };

    render() {
        const columns = [
            {
                title: 'id',
                key: 'id',
                dataIndex: 'id'
            },
            {
                title: '',
                key: 'userName',
                dataIndex: 'userName'
            },
            {
                title: '',
                key: 'sex',
                dataIndex: 'sex',
                render: sex => {
                    return sex === '1' ? '' : '';
                }
            },
            {
                title: '',
                key: 'state',
                dataIndex: 'state',
                render: state => {
                    const config = {
                        '1': '',
                        '2': '',
                        '3': '',
                        '4': 'FE',
                        '5': ''
                    };
                    return config[state];
                }
            },
            {
                title: '',
                key: 'interest',
                dataIndex: 'interest',
                render: interest => {
                    const config = {
                        '1': '',
                        '2': '',
                        '3': '',
                        '4': '',
                        '5': '',
                        '6': '',
                        '7': '',
                        '8': ''
                    };
                    return config[interest];
                }
            },
            {
                title: '',
                key: 'birthday',
                dataIndex: 'birthday'
            },
            {
                title: '',
                key: 'address',
                dataIndex: 'address'
            },
            {
                title: '',
                key: 'time',
                dataIndex: 'time'
            }
        ];
        const { selectedRowKeys, selectedCheckRowKeys } = this.state;
        const rowSelection = {
            type: 'radio',
            selectedRowKeys
        };
        const rowCheckSelection = {
            type: 'checkbox',
            selectedRowKeys: selectedCheckRowKeys,
            onChange: (selectedRowKeys, selectedRows) => {
                this.setState({
                    selectedCheckRowKeys: selectedRowKeys,
                    selectedCheckRows: selectedRows
                });
            }
        }
        return (
            <div style={{width: '100%'}}>
                <Card title="">
                    <Table
                        bordered
                        columns={columns}
                        dataSource={this.state.dataSource}
                        pagination={false}
                    />
                </Card>
                <Card title="-Mock" style={{margin: '10px 0'}}>
                    <Table
                        bordered
                        columns={columns}
                        dataSource={this.state.dataSource2}
                        pagination={false}
                    />
                </Card>
                <Card title="Mock-" style={{margin: '10px 0'}}>
                    <Table
                        bordered
                        rowSelection={rowSelection}
                        onRow={(record, index) => {
                            return {
                                onClick: () => {
                                    this.onRowClick(record, index);
                                }
                            };
                        }}
                        columns={columns}
                        dataSource={this.state.dataSource2}
                        pagination={false}
                    />
                </Card>
                <Card title="Mock-" style={{margin: '10px 0'}}>
                    <div style={{marginBottom: 10}}>
                        <Button onClick={this.handleDelete}></Button>
                    </div>
                    <Table
                        bordered
                        rowSelection={rowCheckSelection}
                        columns={columns}
                        dataSource={this.state.dataSource2}
                        pagination={false}
                    />
                </Card>
                <Card title="Mock-" style={{margin: '10px 0'}}>
                    <Table
                        bordered
                        columns={columns}
                        dataSource={this.state.dataSource2}
                        pagination={this.state.pagination}
                    />
                </Card>
            </div>
        );
    }
}

export default BasicTable;
