import React, { Component } from 'react';
import { Card, Form, Button, Input, Checkbox, Radio, Select, Switch, DatePicker, TimePicker, Upload, InputNumber, message } from 'antd';
import { PlusOutlined } from '@ant-design/icons';
import moment from 'moment';
const FormItem = Form.Item;
const RadioGroup = Radio.Group;
const Option = Select.Option;
const TextArea= Input.TextArea;

class FormRegister extends Component {
    state = {
        loading: false
    };

    handleSubmit = userInfo =>{
        console.log(JSON.stringify(userInfo))
        message.success(`${userInfo.userName} ${userInfo.pwd}`)
    };

    getBase64 = (img, callback)=>{
        const reader = new FileReader();
        reader.addEventListener('load', () => callback(reader.result));
        reader.readAsDataURL(img);
    };

    handleChange = info => {
        if (info.file.status === 'uploading') {
            this.setState({ loading: true });
            return;
        }
        if (info.file.status === 'done') {
            // Get this url from response in real world.
            this.getBase64(info.file.originFileObj, imageUrl => this.setState({
                userImg:imageUrl,
                loading: false,
            }));
        }
    };

    render() {
        const fromItemLayout = {
            labelCol: {
                xs: 24,
                sm: 4
            },
            wrapperCol: {
                xs: 24,
                sm: 12
            }
        };
        const offsetLayout = {
            wrapperCol: {
                xs: 24,
                sm: {
                    span: 12,
                    offset: 4
                }
            }
        };
        const rowObject = {
            minRows: 4,
            maxRows: 6
        };
        return (
            <div style={{width: '100%'}}>
                <Card title="">
                    <Form
                        layout="horizontal"
                        initialValues={{
                            userName: '',
                            pwd: '',
                            sex: '1',
                            age: 18,
                            state: '2',
                            interest: ['2', '5'],
                            isMarried: true,
                            birthday: moment('2018-08-08'),
                            address: '',
                            time: '',
                            userImg: ''
                        }}
                        {...fromItemLayout}
                        onFinish={this.handleSubmit}
                    >
                        <FormItem
                            label=""
                            name="userName"
                            rules={
                                [
                                    { required: true, message: '' }
                                ]
                            }
                        >
                            <Input placeholder="" />
                        </FormItem>
                        <FormItem
                            label=""
                            name="pwd"
                        >
                            <Input type="password" placeholder="" />
                        </FormItem>
                        <FormItem
                            label=""
                            name="sex"
                        >
                            <RadioGroup>
                                <Radio value="1"></Radio>
                                <Radio value="2"></Radio>
                            </RadioGroup>
                        </FormItem>
                        <FormItem
                            label=""
                            name="age"
                        >
                            <InputNumber />
                        </FormItem>
                        <FormItem
                            label=""
                            name="state"
                        >
                            <Select>
                                <Option value="1"></Option>
                                <Option value="2"></Option>
                                <Option value="3"></Option>
                                <Option value="4">FE</Option>
                                <Option value="5"></Option>
                            </Select>
                        </FormItem>
                        <FormItem
                            label=""
                            name="interest"
                        >
                            <Select mode="multiple">
                                <Option value="1"></Option>
                                <Option value="2"></Option>
                                <Option value="3"></Option>
                                <Option value="4"></Option>
                                <Option value="5"></Option>
                                <Option value="6"></Option>
                                <Option value="7"></Option>
                                <Option value="8"></Option>
                            </Select>
                        </FormItem>
                        <FormItem
                            label=""
                        >
                            <FormItem name="isMarried" valuePropName="checked" noStyle>
                                <Switch />
                            </FormItem>
                        </FormItem>
                        <FormItem
                            label=""
                            name="birthday"
                        >
                            <DatePicker />
                        </FormItem>
                        <FormItem
                            label=""
                            name="address"
                        >
                            <TextArea autoSize={rowObject} />
                        </FormItem>
                        <FormItem
                            label=""
                            name="time"
                        >
                            <TimePicker />
                        </FormItem>
                        <FormItem
                            label=""
                        >
                            <Upload
                                name="userImg"
                                listType="picture-card"
                                showUploadList={false}
                                action="//jsonplaceholder.typicode.com/posts/"
                                onChange={this.handleChange}
                            >
                                {this.state.userImg ? <img src={this.state.userImg} width="100" height="100" alt="userImg" /> : <PlusOutlined />}
                            </Upload>
                        </FormItem>
                        <FormItem
                            {...offsetLayout}
                        >
                            <Checkbox><a href="void: 0"></a></Checkbox>
                        </FormItem>
                        <FormItem
                            {...offsetLayout}
                        >
                            <Button type="primary" htmlType="submit"></Button>
                        </FormItem>
                    </Form>
                </Card>
            </div>
        );
    }
}

export default FormRegister;