import React, { Component } from 'react';
import { Card, Button, Table, Form, Modal, message } from 'antd';
import axios from './../../axios';
import Utils from './../../utils';
import BaseForm from './../../components/BaseForm';
import ETable from './../../components/ETable';
const FormItem = Form.Item;

export default class Order extends Component {
    state = {
        list: [],
        selectedRowKeys: [],
        selectedItem: null,
        selectedIds: [],
        orderInfo: null,
        orderConfirmVisible: false
    };
    params = {
        page: 1
    };

    formList = [
        {
            type: 'SELECT',
            label: '',
            field: 'city',
            placeholder: '',
            width: 80,
            list: [
                { id: '0', name: '' },
                { id: '1', name: '' },
                { id: '2', name: '' },
                { id: '3', name: '' }
            ]
        },
        {
            type: ''
        },
        {
            type: 'SELECT',
            label: '',
            field: 'order_status',
            placeholder: '',
            width: 80,
            list: [
                { id: '0', name: '' },
                { id: '1', name: '' },
                { id: '2', name: '' }
            ]
        }
    ];
    initialValues = {
        city: '0',
        'start_time': '',
        'end_time': '',
        order_status: '0'
    };

    componentDidMount() {
        this.requestList();
    }

    onRowClick = (record, index) => {
        let selectKey = [index];
        this.setState({
            selectedRowKeys: selectKey,
            selectedItem: record
        });
    };

    requestList = () => {
        const _this = this;
        axios.requestList(_this, '/order/list', this.params, true);
        // axios.ajax({
        //     url: '/order/list',
        //     data: {
        //         params: this.params
        //     }
        // }).then(res => {
        //     const list = res.result.item_list.map((item, index) => {
        //         item.key = index;
        //         return item;
        //     });
        //     this.setState({
        //         list,
        //         pagination: Utils.pagination(res, current => {
        //             _this.params.page = current;
        //             _this.requestList();
        //         })
        //     })
        // });
    };

    openOrderDetail = () => {
        let item = this.state.selectedItem;
        if (!item) {
            Modal.info({
                title: '',
                content: ''
            });
            return;
        }
        const ORDER_DETAIL_RUL = process.env.NODE_ENV === 'development'
            ? `/#/common/order/detail/${item.id}`
            : `/bicyclemanager/#/common/order/detail/${item.id}`;
        window.open(ORDER_DETAIL_RUL, '_blank');
    };

    // 
    handleConfirm = () => {
        let item = this.state.selectedItem;
        console.log(item);
        if (!item) {
            Modal.info({
                title: '',
                content: ''
            });
            return;
        }
        axios.ajax({
            url: '/order/ebike_info',
            data: {
                params: {
                    orderId: item.id
                }
            }
        }).then(res => {
            if (res.code === 0) {
                this.setState({
                    orderInfo: res.result,
                    orderConfirmVisible: true
                })
            }
        });
    };

    // 
    handleFinishOrder = () => {
        const { selectedItem } = this.state;
        axios.ajax({
            url: '/order/finish_order',
            data: {
                params: {
                    orderId: selectedItem.id
                }
            }
        }).then(res => {
            if (res.code === 0) {
                message.success('');
                this.setState({
                    orderConfirmVisible: false
                });
                this.requestList();
            }
        });
    };

    handleFilter = params => {
        console.log(params);
        this.params = params;
        this.requestList();
    };

    render() {
        const columns = [
            {
                title: '',
                dataIndex: 'order_sn'
            },
            {
                title: '',
                dataIndex: 'bike_sn'
            },
            {
                title: '',
                dataIndex: 'user_name'
            },
            {
                title: '',
                dataIndex: 'mobile'
            },
            {
                title: '',
                dataIndex: 'distance',
                render(distance) {
                    return distance / 1000 + 'Km';
                }
            },
            {
                title: '',
                dataIndex: 'total_time'
            },
            {
                title: '',
                dataIndex: 'status'
            },
            {
                title: '',
                dataIndex: 'start_time'
            },
            {
                title: '',
                dataIndex: 'end_time'
            },
            {
                title: '',
                dataIndex: 'total_fee'
            },
            {
                title: '',
                dataIndex: 'user_pay'
            }
        ];
        const formItemLayout = {
            labelCol: { span: 5 },
            wrapperCol: { span: 19 }
        };
        // const { selectedRowKeys } = this.state;
        // const rowSelection = {
        //     type: 'radio',
        //     wrapperCol: {
        //         span: 19
        //     },
        //     selectedRowKeys
        // };

        return (
            <div style={{width: '100%'}}>
                <Card>
                    <BaseForm formList={this.formList} initialValues={this.initialValues} filterSubmit={this.handleFilter} />
                </Card>
                <Card style={{marginTop: 10}}>
                    <Button type="primary" onClick={this.openOrderDetail}></Button>
                    <Button type="primary" style={{marginLeft: 10}} onClick={this.handleConfirm}></Button>
                </Card>
                <div className="content-wrap">
                    <ETable
                        updateSelectedItem={Utils.updateSelectedItem.bind(this)}
                        columns={columns}
                        dataSource={this.state.list}
                        selectedRowKeys={this.state.selectedRowKeys}
                        selectedIds={this.state.selectedIds}
                        selectedItem={this.state.selectedItem}
                        pagination={this.state.pagination}
                        // rowSelection="checkbox"
                    />
                    {/* <Table
                        bordered
                        columns={columns}
                        dataSource={this.state.list}
                        pagination={this.state.pagination}
                        rowSelection={rowSelection}
                        onRow={(record, index) => {
                            return {
                                onClick: () => {
                                    this.onRowClick(record, index);
                                }
                            };
                        }}
                    /> */}
                </div>
                {
                    this.state.orderConfirmVisible && (
                        <Modal
                            title=""
                            visible={this.state.orderConfirmVisible}
                            onCancel={() => {
                                this.setState({
                                    orderConfirmVisible: false
                                })
                            }}
                            onOk={this.handleFinishOrder}
                            width={600}
                        >
                            <FormItem label="" {...formItemLayout}>
                                {this.state.orderInfo.bike_sn}
                            </FormItem>
                            <FormItem label="" {...formItemLayout}>
                                {this.state.orderInfo.battery + '%'}
                            </FormItem>
                            <FormItem label="" {...formItemLayout}>
                                {this.state.orderInfo.start_time}
                            </FormItem>
                            <FormItem label="" {...formItemLayout}>
                                {this.state.orderInfo.location}
                            </FormItem>
                        </Modal>
                    )
                }
            </div>
        );
    }
}