class City extends Component {
    state = {
        list: [],
        isShowOpenCity: false
    };
    params = {
        page: 1
    };
    formRef = React.createRef();

    componentDidMount() {
        this.requestList();
    }

    // mock
    requestList = () => {
        let _this = this;
        axios.ajax({
            url: '/open_city',
            data: {
                params: {
                    page: this.params.page
                }
            }
        }).then(res => {
            let list = res.result.item_list.map((item, index) => ({
                ...item,
                key: index
            }));
            this.setState({
                list,
                pagination: Utils.pagination(res, current => {
                    _this.params.page = current;
                    _this.requestList();
                })
            })
        });
    };

    // 
    handleOpenCity = () => {
        this.setState({
            isShowOpenCity: true
        });
    };

    // 
    handleSubmit = () => {
        console.log(this.formRef.current.getFieldsValue());
        let cityInfo = this.formRef.current.getFieldsValue();
        axios.ajax({
            url: '/city/open',
            data: {
                params: cityInfo
            }
        }).then(res => {
            if (res.code === 0) {
                message.success('');
                this.setState({
                    isShowOpenCity: false
                });
                this.requestList();
            }
        })
    };

    render() {
        const columns = [
            {
                title: 'ID',
                dataIndex: 'id'
            },
            {
                title: '',
                dataIndex: 'name'
            },
            {
                title: '',
                dataIndex: 'mode',
                render(mode) {
                    return mode === 1 ? '' : '';
                }
            },
            {
                title: '',
                dataIndex: 'op_mode',
                render(op_mode) {
                    return op_mode === 1 ? '' : '';
                }
            },
            {
                title: '',
                dataIndex: 'franchisee_name'
            },
            {
                title: '',
                dataIndex: 'city_admins',
                render(arr) {
                    return arr.map(item => item.user_name).join(',');
                }
            },
            {
                title: '',
                dataIndex: 'open_time'
            },
            {
                title: '',
                dataIndex: 'update_time',
                render: Utils.formateDate
            },
            {
                title: '',
                dataIndex: 'sys_user_name'
            }
        ];
        return (
            <div style={{width: '100%'}}>
                <Card>
                    <FilterForm />
                </Card>
                <Card style={{marginTop: 10}}>
                    <Button type="primary" onClick={this.handleOpenCity}></Button>
                </Card>
                <div className="content-wrap">
                    <Table
                        bordered
                        columns={columns}
                        dataSource={this.state.list}
                        pagination={this.state.pagination}
                    />
                </div>
                <Modal
                    title=""
                    visible={this.state.isShowOpenCity}
                    onCancel={() => {
                        this.setState({
                            isShowOpenCity: false
                        })
                    }}
                    onOk={this.handleSubmit}
                >
                    <OpenCityForm formRef={this.formRef} />
                </Modal>
            </div>
        );
    }
}

export default City;
