import React, { Component } from 'react';
import { Card, Form, Input, Button, message, Checkbox } from 'antd';
import { UserOutlined, LockOutlined } from '@ant-design/icons';
const FormItem = Form.Item;

class FormLogin extends Component {
    handleSubmit = userInfo => {
        message.success(`${userInfo.userName} ${userInfo.pwd}`);
    };

    render() {
        return (
            <div style={{width: '100%'}}>
                <Card title="">
                    <Form layout="inline">
                        <FormItem>
                            <Input placeholder="" />
                        </FormItem>
                        <FormItem>
                            <Input placeholder="" />
                        </FormItem>
                        <FormItem>
                            <Button type="primary"></Button>
                        </FormItem>
                    </Form>
                </Card>
                <Card title="" style={{marginTop: 10}}>
                    <Form
                        style={{width: 300}}
                        initialValues={{
                            userName: '',
                            pwd: '',
                            remember: true
                        }}
                        onFinish={this.handleSubmit}
                    >
                        <FormItem
                            name="userName"
                            rules={
                                [
                                    { required: true, message: '' },
                                    { min: 5, max: 10, message: ' 5  10 ' },
                                    { pattern: new RegExp('^\\w+$', 'g'), message: '' }
                                ]
                            }
                        >
                            <Input prefix={<UserOutlined />} placeholder="" />
                        </FormItem>
                        <FormItem
                            name="pwd"
                        >
                            <Input prefix={<LockOutlined />} type="password" placeholder="" />
                        </FormItem>
                        <FormItem>
                            <FormItem name="remember" valuePropName="checked" noStyle>
                                <Checkbox></Checkbox>
                            </FormItem>
                            <a href="void: 0" style={{float: 'right'}}></a>
                        </FormItem>
                        <FormItem>
                            <Button type="primary" htmlType="submit"></Button>
                        </FormItem>
                    </Form>
                </Card>
            </div>
        );
    }
}

export default FormLogin;