import React, { Component } from 'react';
import { Card, Button, Radio } from 'antd';
import { PlusOutlined, EditOutlined, DeleteOutlined, SearchOutlined, DownloadOutlined, LeftOutlined, RightOutlined } from '@ant-design/icons';
import './ui.less';

export default class Buttons extends Component {
    state = {
        loading: true,
        size: 'default'
    };

    handleCloseLoading = () => {
        this.setState({
            loading: false
        });
    }

    handleChange = e => {
        this.setState({
            size: e.target.value
        });
    }

    render() {
        return (
            <div style={{width: '100%'}}>
                <Card title="" className="card-wrap">
                    <Button type="primary">Primary Button</Button>
                    <Button>Default Button</Button>
                    <Button type="dashed">Dashed Button</Button>
                    <Button type="danger">Danger Button</Button>
                    <Button disabled>Disabled Button</Button>
                </Card>
                <Card title="" className="card-wrap">
                    <Button icon={<PlusOutlined />}></Button>
                    <Button icon={<EditOutlined />}></Button>
                    <Button icon={<DeleteOutlined />}></Button>
                    <Button shape="circle" icon={<SearchOutlined />}></Button>
                    <Button type="primary" icon={<SearchOutlined />}></Button>
                    <Button type="primary" icon={<DownloadOutlined />}></Button>
                </Card>
                <Card title="Loading" className="card-wrap">
                    <Button type="primary" loading={this.state.loading}></Button>
                    <Button type="primary" shape="circle" loading={this.state.loading}></Button>
                    <Button loading={this.state.loading}></Button>
                    <Button shape="circle" loading={this.state.loading}></Button>
                    <Button type="primary" onClick={this.handleCloseLoading}></Button>
                </Card>
                <Card title="">
                    <Button.Group>
                        <Button type="primary" icon={<LeftOutlined />}></Button>
                        <Button type="primary" icon={<RightOutlined />}></Button>
                    </Button.Group>
                </Card>
                <Card title="" className="card-wrap">
                    <Radio.Group value={this.state.size} onChange={this.handleChange}>
                        <Radio value="small"></Radio>
                        <Radio value="default"></Radio>
                        <Radio value="large"></Radio>
                    </Radio.Group>
                    <Button type="primary" size={this.state.size}>Primary Button</Button>
                    <Button size={this.state.size}>Default Button</Button>
                    <Button type="dashed" size={this.state.size}>Dashed Button</Button>
                    <Button type="danger" size={this.state.size}>Danger Button</Button>
                </Card>
            </div>
        );
    }
};
